@import '@components/input/input.scss';

.form {
    max-width: 394px;
    height: 387px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    background-color: $color-sand-brown;
    border-radius: 20px;
    padding: 40px;
    position: relative;
    overflow: hidden;
    z-index: 1;

    &__content {
        z-index: 3;
    }

    &__title {
        @include title-m;

        margin-bottom: 20px;
    }

    &__inputs {
        display: flex;
        flex-direction: column;
        row-gap: 10px;
        margin-bottom: 20px;
    }

    &__background-dune {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        z-index: 2;

        svg {
            position: relative;
            right: 136px;
            top: 10px;
            width: 673px;
            height: 224px;
        }
    }
}