.contact-info {
    display: flex;
    gap: 20px;
    width: 100%;

    &__content{
        display: flex;
        flex-direction: column;
        max-width: 214px;
    }

    &__title {
        @include title-s;

        color: $color-red;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        row-gap: 20px;
    }

    &__item {
        display: flex;
        column-gap: 16px;
    }

    &__text {
        @include body;

        color: $color-white;
    }

    &__icon {
        width: 28px;
        height: 28px;
    }
}