@import '@components/advantege-item/advantege-item.scss';

.main-advantages {
    @include container;

    padding: 40px;
    border-radius: 20px;
    background-image: url('@images/advantages.png');
    background-repeat: no-repeat;
    background-size: cover;

    @include mobile {
        padding: 40px 20px;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
        
        @include mobile {
            flex-direction: column;
            row-gap: 20px;
            margin-bottom: 20px;
        }
    }

    &__header-text {
        display: flex;
        flex-direction: column;
        row-gap: 10px;
    }

    &__title {
        @include title-l();

        color: $color-white;

        @include mobile {
            @include title-m();
        }
    }

    &__description {
        @include desktop {
            @include body();
        }
        max-width: 556px;
        color: $color-white;
    }

    &__cards {
        display: flex;
        flex-direction: column;
        row-gap: 4px;
    }
}