@import '@components/button/button.scss';
@import '@styles/variables.scss';
@import '@styles/mixins.scss';

.category-card {
  padding: 20px;
  background: $color-white;
  border-radius: 20px;
  width: 100%;
  text-align: center;
  position: relative;
  transition: 0.3s all;
  display: flex;
  min-height: 300px;
  flex-direction: column;

  &__title {
    color: $color-dark-blue;

    @include title-m;

    @include mobile {
      @include title-s;
    }
  }

  &__description {
    margin-top: 10px;
    margin-bottom: 18px;
    color: $color-gray;

    @include body;

    @include mobile {
      @include body-14;
    }
  }

  &__shape {
    position: absolute;
    left: 50%;
    top: 60%;
    transform: translate(-50%, -50%);
    width: 192px;
    height: 192px;
    border: 1px solid $color-sand-brown;
    border-radius: 50%;
    transition: 0.3s all;
    bottom: 40px;

    @include flex-center;

    & > div {
      width: 148px;
      height: 148px;
      border: 1px solid $color-sand-brown;
      border-radius: 50%;
      transition: 0.3s all;

      @include flex-center;

      & > div {
        width: 100px;
        height: 100px;
        border: 1px solid $color-sand-brown;
        border-radius: 50%;
        transition: 0.3s all;

        @include flex-center;
      }
    }
  }

  &__img {
    width: 100%;
    max-width: 354px;
    margin: 0 auto;
    height: auto;
    margin-top: auto;
    position: relative;
    z-index: 2;
    transition: 0.3s all;
  }

  @include hover {
    box-shadow: 0px 16px 24px -8px #6B707533;

    .category-card__img {
      transform: scale(1.1);
    }

    .category-card__shape {
      width: 334px;
      height: 334px;
      bottom: 0px;
      border-color: rgba($color-sand-brown, 0.3);

      & > div {
        width: 260px;
        height: 260px;

        border-color: rgba($color-sand-brown, 0.6);

        & > div {
          width: 188px;
          height: 188px;
        }
      }
    }
  }
}