@import '@styles/mixins.scss';

.button {
    border-radius: 10px;
    height: 48px;
    padding: 0 20px;
    border: none;
    background-color: $color-red;
    color: $color-white;
    font-weight: 500;
    width: fit-content;
    cursor: pointer;

    &:hover {
        background-color: $color-red-hover;
    }

    &--with-outline {
        background-color: $color-white;
        border: 1px solid $color-red;
        

        &:hover {
            svg {
                @include svg-color($color-white);
            }
        }
    }

    &--with-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 12px;
    }
}

.button.button--with-icon.button--with-outline {
    padding: 0 11px;
}