@import '@components/category-card/category-card.scss';
@import '@styles/mixins.scss';
@import '@styles/variables.scss';

@keyframes sun-move {
  0% {
    top: 50%;
    right: 10%;
  }

  50% {
    top: 50%;
    right: 10%;
  }

  100% {
    top: 10%;
  }
}

.banner {
  padding-top: 160px;
  position: relative;

  @include mobile {
    padding-top: 110px;
  }

  &__background {
    position: absolute;
    width: 100vw;
    top: 0;
    left: 0;
    height: 100%;
    z-index: -1;
  }

  &__background-inner {
    position: relative;
    height: 500px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile {
      overflow: hidden;
    }
  }

  &__background-gradient {
    background: linear-gradient(180deg, rgba(217, 203, 184, 0) 0%, #D9CBB8 29.17%, #D9CBB8 39.06%, rgba(217, 203, 184, 0.45) 63.54%, rgba(217, 203, 184, 0) 96.87%);
    height: 382px;
    position: relative;
    top: 0;
    width: 100%;

    @include mobile {
      top: -100px;
    }
  }

  &__background-dune {
    width: 100%;
    position: absolute;
    bottom: -200px;
    left: 0;
    right: 0;

    svg {
      width: 100%;
    }

    @include mobile {
      width: 100vh;
    }
  }

  &__background-sun {
    position: absolute;
    top: 10%;
    right: 20%;
    animation: sun-move;
    animation-duration: 2s;
    animation-iteration-count: 1;

    svg {
      width: 300px;
    }

    @include mobile {
      display: none;
    }
  }

  &__content {
    margin-top: 90px;
    
    @include container;
  }

  &__title {
    color: $color-black;
  
    @include title-xl;

    @include mobile {
      @include title-m;
    }
  }

  &__subtitle {
    margin-top: 10px;
    max-width: 594px;
    color: $color-gray;

    @include subtitle;

    @include mobile {
      @include body;
    }
  }

  &__button {
    margin-top: 40px;
  }

  &__category-list {
    margin-top: 80px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;

    @include mobile {
      margin: 100px auto 0 auto;
      flex-wrap: wrap;
      @include flex-center;
    }
  }
}