@import './contact-info/contact-info.scss';

.contact-card {
    max-width: 798px;
    background-color: $color-black;
    border-radius: 20px;
    padding: 30px;
    width: max-content;

    &__header {
        display: flex;
        column-gap:  14px;
        margin-bottom: 40px;
    }

    &__title {
        @include title-l;
        color: $color-white;
        flex-grow: 1 ;
    }
}