@import '@styles/mixins.scss';

.brand-list {
  display: flex !important;
  gap: 24px;
  justify-content: space-between;
  align-items: center;
  list-style: none;

  &--view-desktop {
    display: none !important;
    @include desktop {
      display: flex !important;
    }
  }

  &__item {
    @include hover {
      .brand-list__image {
        filter: grayscale(0%);
        transform: scale(1.1);
      }
    }
  }

  &__image {
    transition: 0.3s;

    width: (calc(100vh / 12));
    filter: grayscale(0%);

    @include desktop {
      width: initial;
      filter: grayscale(100%);
    }
  }
}

.marquee3k__wrapper {
  display: flex;
  align-items: center;
  gap: 20px;

  @include desktop {
    display: none;
  }
}
