.advantege-item {
    display: flex;
    min-height: 80px;
    background-color: $color-white;
    border-radius: 10px;
    padding: 20px;
    column-gap: 40px;

    @include mobile {
        flex-direction: column;
        row-gap: 10px;
    }

    &__number {
        font-weight: 700;
        color: $color-red;
    }

    &__title-wrapper {
        display: flex;
        column-gap: 20px;
        max-width: 442px;
        width: 100%;
        align-items: center;
        flex-shrink: 0;

        @include tablet {
            max-width: 320px;
        }

        @include mobile {
            column-gap: 10px;
            max-width: none;
            flex-shrink: 1;
        }

        svg {
            display: flex;
            flex-shrink: 0;
 
            @include mobile {
                display: none;
            }
        }
    }

    &__title {
        flex-grow: 1;
        font-weight: 700;
    }

    &__text {
        color: $color-gray;
    }
}
