@import '@styles/mixins.scss';

.case-card {
  padding: 20px 10px 10px 10px;
  position: relative;
  border-radius: 20px;

  &__photo {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
  }

  &__content {
    position: relative;
    z-index: 2;
  }

  &__brand-wrapper {
    padding: 12px;
    height: 50px;
    width: fit-content;
    border-radius: 10px;
    margin-left: 10px;
    background: $color-white;

    @include flex-center;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-radius: 10px;
    border: none;
    background: #FBFAF8B2;
    width: 100%;
    margin-top: 211px;
    transition: 0.3s;

    @include title-s;

    @include hover {
      background: #FBFAF8E5;
    }

    @include mobile {
      padding: 18px 10px;
      justify-content: center;

      @include body-14;
    }

    svg {
      @include mobile {
        display: none;

      }
    }
  }

  &__modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    z-index: 100;

    &--opened {
      display: block;
    }
  }

  &__modal-mask {
    background: rgba(#333, 0.5);
    width: 100%;
    height: 100%;
    @include flex-center;
  }

  &__modal-content {
    width: 80%;
    height: 80vh;
    overflow: scroll;
    background: $color-white;
  }
}