$screen-mobile-to: 768px;
$screen-tablet-from: 769px;
$screen-tablet-to: 1025px;
$screen-desktop-from: 1026px;

@mixin svg-color($color, $transition-duration: 0ms) {
  transition: all $transition-duration;
  fill: $color;

  path {
    transition: all $transition-duration;
    fill: inherit;
  }
}

$screen-mobile-to: 768px;
$screen-tablet-from: 769px;
$screen-tablet-to: 1025px;
$screen-desktop-from: 1026px;

@mixin mobile {
  @media screen and (max-width: #{$screen-mobile-to}) {
    @content;
  }
}

@mixin mobile-and-tablet {
  @media screen and (max-width: #{$screen-tablet-to}) {
    @content;
  }
}

@mixin tablet {
  @media screen and (min-width: #{$screen-tablet-from}) and (max-width: #{$screen-tablet-to}) {
    @content;
  }
}

@mixin modal-hide {
  @media screen and (min-width: #{$screen-tablet-from}) {
    @content;
  }
}

@mixin desktop { 
  @media screen and (min-width: #{$screen-tablet-to}) { 
      @content; 
  }
}

@mixin hover {
  @media (any-hover: hover) {
    &:hover {
      @content;
    }
  }
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin container($max-width: 1250px) {
  max-width: $max-width;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  @include mobile {
    padding: 0 10px;
  }
}

@mixin title-xl {
font-size: 56px;
font-weight: 700;
line-height: 67px;
}

@mixin title-l {
  font-size: 40px;
  line-height: 48px;
  font-weight: 700;
}

@mixin title-s {
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
}

@mixin title-m {
  font-size: 24px;
  line-height: 28px;
  font-weight: 700;
}

@mixin title-s {
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
}

@mixin subtitle {
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}

@mixin body {
  font-size: 16px;
  line-height: 22px;
}

@mixin body-14 {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}
@mixin body-2 {
  font-size: 14px;
  line-height: 21px;
}

@mixin small-text {
  font-size: 12px;
  line-height: 12px;
}