@import '@src/assets/styles/main.scss';
@import '@components/banner/banner.scss';
@import '@components/brand-list/brand-list.scss';
@import '@src/assets/styles/mixins.scss';
@import '@components/main-advantages/main-advantages.scss';
@import '@components/cases-block/cases-block.scss';
@import '@components/contact-card/contact-card.scss';
@import '@components/form/form.scss';

h1 {
  color: black;
}

.pug-logo {
  width: 200px;
}

.main {
  overflow: hidden;
  &__brand-list {
    @include container;
    margin-top: 80px;

    @include mobile {
      @include container(100%);
      margin-top: 40px;
    }
  }

  &__advantages {
    @include container;
    margin-top: 80px;

    @include mobile {
      margin-top: 60px;
    }
  }

  &__cases-block {
    @include container;
    margin-top: 40px;
  }

  &__contacts {
    @include container;

    display: flex;
    column-gap: 10px;
    padding: 0;
  }

  &__contacts {
    @include container;
    margin-top: 40px;
  }
}