@import '@components/case-card/case-card.scss';

.cases-block {
  &__top {
    display: flex;
    justify-content: space-between;
    max-width: 100%;
  }

  &__title {
    @include title-l;

    @include mobile {
      @include title-m;
    }
  }

  &__controls {
    position: relative;
    margin-left: auto;
    border: 1px solid $color-red;
    width: 92px;
    border-radius: 10px;
    overflow: hidden;
    height: 46px;
    @include flex-center;
  }

  &__list {
    margin-top: 20px;
  }

  .swiper-button-prev {
    left: 0;
    width: 46px;
    height: 48px;
    top: -3px;
    transform: translateY(50%);
    transition: 0.3s;

    &::after {
      display: none;
    }

    svg {
      width: 24px;
      transition: 0.3s;
    }

    @include hover {
      background: $color-red;

      svg {
        @include svg-color(var(--white, 300ms));
      }
    }
  }
  
  .swiper-button-next {
    width: 46px;
    height: 48px;
    top: -3px;
    right: 0;
    transform: translateY(50%);
    transition: 0.3s;
    

    &::after {
      display: none;
    }

    svg {
      transform: rotate(180deg);
      width: 24px;
      transition: 0.3s;
    }

    @include hover {
      background: $color-red;

      svg {
        @include svg-color(var(--white, 300ms));
      }
    }
  }
}